<template>
    <a :href="computedURL" :class="[header.class]" target="_blank">
        <span class="subtitle-2 font-weight-bold" >
            {{ format($getNestedValue(item, header.value))  }} 
        </span>
    </a>
 
</template>
<script>


export default {
    name:'TableFieldSparkline',
    props: {
        url: {
            type: String,
            default: '#'
        },
        item: {
            type: Object,
        },
        header: {
            type: Object
        }
    },
    computed: {
        computedURL () {
            const { url } = this
            if (url.substr(0, 1) == '$') {
                return this.$getNestedValue(this.item, url.substr(1))
            } 
            return url
        },
    },
    methods: {
        format (value) {
            if (this.$options.filters[this.header.format]) { 
                return this.$options.filters[this.header.format](value)
            }
            return value
        }
    } 
}
</script>

